<template>
  <div class="superAdmin">
    <div class="top">
      <div class="search">
        <el-input v-model="keywords" @input="search" placeholder="请输入搜索内容">
          <i
             v-show="keywords"
             class="el-icon-close"
             style="margin-top:12px"
             slot="suffix"
             @click="clearInput">
          </i>
        </el-input>
      </div>
      <div class="topRight">
        <div class="addUser">
          <el-button type="primary" @click="addDialogVisible = true">新增用户</el-button>
          <el-dialog
            custom-class="addUserDialog"
            :visible.sync="addDialogVisible"
            width="30%"
            :before-close="handleClose">
            <el-input v-model="newUser" placeholder="请输入用户名"></el-input>
            <el-select v-model="newAuthValue" placeholder="请选择权限">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <br>
            <el-button @click="saveNewUser" type="warning" align="center">保存</el-button>
          </el-dialog>
        </div>
        <el-button @click="delMulUserBtn" size="mini" style="height:41px;margin-left:10px" type="danger">删除用户</el-button>
        <el-dialog
          title="提示"
          :visible.sync="delMulUserDialogVisible"
          width="30%"
          :before-close="handleClose">
          <span>确认删除{{ delTotalNum }}人？</span>
          <span slot="footer" class="dialog-footer">
           <el-button @click="dialogVisible = false">取 消</el-button>
           <el-button @click="delSure" type="primary">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </div>
    <!-- 表格 -->
    <el-table
      :data="userList"
      height="100%"
      border
      stripe
      highlight-current-row
      @selection-change="handleSelectionChange"
      style="width: 97%">
      <el-table-column
        type="selection" width="100">
      </el-table-column>
      <el-table-column
        type="index"
        :index="countPageIndex"
        label="序号"
        align="center"
        width="150">
      </el-table-column>
      <el-table-column
        prop="userName"
        label="用户名"
        align="center">
      </el-table-column>
      <el-table-column
        prop="auth"
        align="center"
        label="权限">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope" >
          <el-button size="mini" @click="editUser(scope.row)">修改</el-button>
          <el-dialog
            append-to-body
            :visible.sync="editDialogVisible"
            width="30%"
            :before-close="handleClose">
            <el-input v-model="currentName"  :disabled="true"></el-input>
            <el-select v-model="currentAuth"  placeholder="请选择权限">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
             <el-button @click="saveAsChange" type="warning" align="center">保存</el-button>
          </el-dialog>
          <el-button size="mini" @click="delUser(scope.row)" type="danger">删除</el-button>
          <el-dialog
           title="提示"
           :visible.sync="delUserdialog"
           width="30%"
           :before-close="handleClose">
           <span>确认删除该用户？</span>
           <span slot="footer" class="dialog-footer">
             <el-button @click="dialogVisible = false">取 消</el-button>
             <el-button @click="delOneSure" type="primary">确 定</el-button>
           </span>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="totalDataNum"
      layout="total,prev, pager, next">
    </el-pagination>
  </div>
</template>

<script>
import { ShowUserList,DelUser,ChangeUser,AddNewUser,AllAuth } from '@/api/request'
import { Message } from 'element-ui'
export default {
    data() {
      return {
        delTotalNum:0,
        totalDataNum:0,
        keywords:'',
        newUser:'',
        authValue:'',
        newAuthValue:'',
        editDialogVisible: false,
        addDialogVisible: false,
        delUserdialog:false,
        dialogVisible:false,
        delMulUserDialogVisible:false,
        currentName:'',
        currentAuth:"",
        userId:'',
        userIDList:[],
        oneuserIDList:[],
        currentPage:1,
        pageSize:10,
        options: [],
        userList: []
      }
  },
  mounted(){
    this.getUserList()
    this.getAllAuth()
  },
  methods:{
    clearInput(){
      this.keywords = ''
      this.currentPage = 1
      this.getUserList()
    },
    saveNewUser(){
      let param = {
        'newUserName':this.newUser,
        'auth':this.newAuthValue
      }
      AddNewUser(param).then((res) =>{
        console.log('新增用户res',res);
        if(res.data.result == 1){
          Message.success(res.data.msg)
          this.addDialogVisible = false
          this.newUser = ''
          this.newAuthValue = ''
          this.getUserList()
          
        }else{
          Message.error(res.data.msg)
        }
      })
    },
    getAllAuth(){
      AllAuth().then((res) =>{
        console.log('权限res',res);
        if(res.data.result == 1){
          this.options = res.data.respData
        }
      })
    },
    editUser(row){
      console.log('row',row);
      this.editDialogVisible = true
      this.userId = row.userId
      this.authValue = row.auth
      this.currentAuth = row.auth
      this.currentName = row.userName
    },
    saveAsChange(){
      let param = {
        'auth':this.currentAuth,
        'userID':this.userId,
      }
      ChangeUser(param).then((res) =>{
        console.log('修改权限res',res);
        if(res.data.result == 1){
          Message.success(res.data.msg)
          this.getUserList()
          this.editDialogVisible = false
        }else{
          Message.err
          or(res.data.msg)
        }
      })
    },
    delMulUserBtn(){
      this.delMulUserDialogVisible = true
    },
    delUser(row){
      this.delUserdialog = true
      this.oneuserIDList.push(row.userId)
      console.log('单选删除列表',this.oneuserIDList);
    },
    handleClose(done){
      this.newUser = ''
      this.newAuthValue = ''
      done();
    },
    handleSizeChange(SizeChange){ 
          console.log('SizeChange',SizeChange);
          this.pageSize = SizeChange
          this.getUserList()
          },
    handleCurrentChange(currentPage){
      this.currentPage = currentPage;
      this.getUserList()
      console.log('用户列表当前页',this.currentPage); //点击第几页
    },
    // 分页后序号叠加
    countPageIndex(index){
      return (this.currentPage - 1) * this.pageSize + index + 1
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log('勾选的这一行的数据',this.multipleSelection);
      this.delTotalNum = this.multipleSelection.length
      this.multipleSelection.forEach((i) =>{
        this.userIDList.push(i.userId)
      })
      console.log('勾选的删除数组',this.userIDList);
    },
    getUserList(){
      let param = {
        'keywords':this.keywords,
        'currentPage':this.currentPage,
        'pageSize':this.pageSize
      }
      console.log('param',param);
      ShowUserList(param).then((res) =>{
        if(res.data.result == 1){
          this.userList = res.data.respData.dataDetail
          this.totalDataNum = res.data.respData.totalDataNum
          if(!this.userList.length && this.currentPage !== 1){
            this.currentPage -=1;
            this.getUserList()
          }
        }
      })
    },
    delSure(){
      DelUser({'userIDList':this.userIDList}).then((res) =>{
        if(res.data.result == 1){
          this.userIDList = []
          Message.success(res.data.msg)
          this.delMulUserDialogVisible = false
          this.getUserList()
        }else{
          Message.error(res.data.msg)
        }
      })
    },
    delOneSure(){
      DelUser({'userIDList':this.oneuserIDList}).then((res) =>{
        if(res.data.result == 1){
          this.oneuserIDList = []
          Message.success(res.data.msg)
          this.getUserList()
          this.delUserdialog = false
        }else{
          Message.error(res.data.msg)
        }
      })
    },
    search(){
        this.currentPage = 1
        this.getUserList()
    }
  },
}
</script>

<style lang="scss" scoped>
    .superAdmin{
        height: 73%;
        width: 100%;
        margin-top: 1rem;
      .top{
        display: flex;
        justify-content: space-between;
        .search{
          width: 23%;
          display: flex;
        }
        .topRight{
          display: flex;
        }
      }
        .addUser{
            text-align: left;
            margin-bottom: 1rem;
            .addUserDialog{
              width: 30% !important;
            }
        }
    }
</style>


